        return new Promise(resolve => {
          vm.$store.dispatch('loadInitResource').then(result => {
            let param = {
              headers: {
              }
            }
            axios.get(url, param).then(response => {
              let items = []
              if (response.data.hasOwnProperty('data')) {
                items = response.data.data
              } else {
