        key: 'smsNotify'
      }
      vm.$store.dispatch('getNotifyConfig', filter1).then(result => {
        if (String(result) === 'true') {
          vm.smsNotify = true
        } else {
          vm.smsNotify = false
        }
      })
      let filter2 = {
        dossierId: id,
        key: 'emailNotify'
      }
      vm.$store.dispatch('getNotifyConfig', filter2).then(result => {
        vm.emailNotify = result
        if (String(result) === 'true') {
          vm.emailNotify = true
        } else {
          vm.emailNotify = false
        }
      })
